*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  height: 100%;
  background: url(../img/site-bg.jpg) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

body {
  font-family: "Helvetica", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  height: 100%;
}
.page-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  align-items: center;
}

header {
  width: 100%;
  background: white;
}

nav {
  padding: 2rem 0 1rem;
  max-width: 71.1875rem;
  margin: 0 auto;
}

nav ul {
  height: 3rem;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav ul li a {
  text-decoration-color: transparent;
  color: dimgray;
}

nav ul li a:hover {
  text-decoration-color: #ea215a;
  text-decoration-thickness: 0.125em;
  text-underline-offset: 0.75rem;
}

img.logo-main {
  width: 100%;
  max-width: 50rem;
}

.content-title {
  font-size: 3.125rem;
  max-width: 71.1875rem;
}

.content-grid {
  display: grid;
  grid-template-columns: 2fr 1fr;
  max-width: 71.1875rem;
  grid-column-gap: 3.75rem;
}

.content-columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 3.75rem;
}

.content-lead {
  font-size: 1.6875rem;
  line-height: 2.6875rem;
  margin-top: 0;
}

.content-columns p {
  font-size: 0.9375rem;
  line-height: 1.5625rem;
}

footer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  min-height: 6rem;
  max-width: 71.1875rem;
  margin: 0 auto;
  padding: 1rem 0;
}

footer > div {
  display: flex;
  column-gap: 1rem;
}

#chas-logo {
  width: 7.75rem;
}

#construction-logo {
  width: 10.625rem;
}

#gallant-logo {
  width: 17rem;
}


@media screen and (max-width: 1279px) {
  .page-wrapper, .content-grid, .content-columns {
    display: block;
  }

  .content-wrapper, footer {
    padding: 0 2rem;
  }
  nav {
    padding: 2rem 2rem 1rem;
  }

  .content-grid, .content-title, .page-wrapper {
    margin-left: auto;
    margin-right: auto;
  }

  .content-wrapper img {
    margin: 2rem auto 2rem;
    max-width: 12.5rem;
    display: block;
  }
}

@media screen and (max-width: 799px) {
  nav {
    /* display: none; */
  }
}

@media screen and (max-height: 880px) {
  .page-wrapper, .content-grid, .content-columns {
    display: block;
  }

  .content-grid, .content-title, .page-wrapper {
    margin-left: auto;
    margin-right: auto;
  }
}